import React, { useEffect, useState } from 'react'
import {
    CButton,
    CButtonGroup,
    CCard,
    CCardBody,
    CCardHeader,
    CCardTitle,
    CCol,
    CForm,
    CFormInput,
    CFormSelect,
    CRow,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilArrowCircleTop, cilFile } from '@coreui/icons'
import { FaBeer } from 'react-icons/fa';
import axios from 'axios';
import Table from '../clients/Table';
const url = 'https://yog-seven.vercel.app'


const EmpDocuments = () => {
    const [action1, setAction1] = useState(false)
    const [document, setDocument] = useState()
    const [image, setImage] = useState()
    const [staffName, setStaffName] = useState('')

    let user = JSON.parse(localStorage.getItem('user-info'))
    const token = user.token;
    const username = user.user.username;
    const centerCode = user.user.centerCode;

    useEffect(() => {
        getStaff()
    }, [])
    const header = [

        /* 
        value: keyword for normal value passing
        btn: keyword for button
        btn1 to btn4: keyword for component passing
        lebel: keyword for anchor tag
        Note: please don't pass empty values or perameters
        */

        { heading: 'Sr.No', uni: '' },
        { heading: 'Name', value: 'FullName' },
        { heading: 'Mobile', value: 'ContactNumber' },
        { heading: 'Email Id', value: 'EmailAddress' },
        { heading: 'Gender', value: 'Gander' },
        { heading: 'Address', value: 'address' },
        { heading: 'Sources', value: 'PayoutType' },
        { heading: 'Department', value: 'Department' },
        { heading: 'Desgantion', value: 'JobDesignation' },
        { heading: 'Resume', link: 'resume' },
        { heading: 'Action', com: (<> <FaBeer size='20px' /></>) },
    ]

    const Users = [
        {
            id: 1,
            date_time: "25-08-2022 03:00 PM",
            member_name: "Nayana Nagrecha",
            mobile: "9136123476",
            service_name: "Yoga",
            variation_name: "3 Months",
            expiry_date: "31-08-2022",
            sales_rep: "Sejal Ganatra",
            pt_trainer: "-",
            trainer: "Prabha Yadav",
            staff_name: "Sejal Ganatra",
        },
        {
            id: 2,
            date_time: "25-08-2022 03:00 PM",
            member_name: "Nayana Nagrecha",
            mobile: "9136123476",
            service_name: "Yoga",
            variation_name: "3 Months",
            expiry_date: "31-08-2022",
            sales_rep: "Sejal Ganatra",
            pt_trainer: "-",
            trainer: "Prabha Yadav",
            staff_name: "Sejal Ganatra",
        },
    ];


    const handleImage = (e) => {
        if (e.target.files[0]) {
            const image1 = e.target.files[0];
            setImage(image1)
        }
    }

    const [staff, setStaff] = useState([])
    function getStaff() {
        axios.get(`${url}/employeeForm/all`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                setStaff(res.data)
                console.log(res.data);
            })
            .catch((error) => {
                console.error(error)
            })
    }

    return (
        <CRow>
            <CCol lg={12} sm={12}>
                <CCard className="mb-3 border-success">
                    <CCardHeader style={{ backgroundColor: '#0B5345', color: 'white' }}>
                        <CCardTitle className="mt-2">Employee Documents</CCardTitle>
                    </CCardHeader>
                    <CCardBody>
                        <CRow className='d-flex mb-2'>
                            <CCol lg={3} sm={6} className='mb-2'>
                                <CFormSelect
                                    id="inputGroupSelect04"
                                    aria-label="Example select with button addon"
                                >
                                    <option>Name</option>
                                    <option >Location</option>
                                    <option >Mobile</option>
                                    <option >Email</option>
                                    <option >Department</option>
                                    <option >Desgantion</option>
                                </CFormSelect>
                            </CCol>

                            <CCol lg={3} sm={6} className='mb-2'>
                                <CFormInput
                                    type='date'
                                    placeholder="Search"
                                    aria-label="Recipient's username"
                                    aria-describedby="button-addon2"
                                />
                            </CCol>
                            <CCol>
                                <CButton type="button" color="primary">
                                    Search
                                </CButton>
                            </CCol>
                            <CCol lg={4} sm={6} className='mb-2' >
                                <CButtonGroup className='float-end'>
                                    <CButton color="primary">
                                        <CIcon icon={cilArrowCircleTop} />
                                        {' '}Export
                                    </CButton>
                                </CButtonGroup>
                            </CCol>
                        </CRow>
                        <CForm className="mb-2">
                            <div className="d-flex justify-content-between">
                                <div></div>
                                <div>
                                    <CRow>
                                        <CCol>
                                            <CButton className="ms-1 mt-2" onClick={() => setAction1(!action1)}>{action1 ? 'close' : 'Upload Document'}</CButton>
                                        </CCol>
                                    </CRow>
                                </div>
                            </div>
                            {action1 &&
                                <div>
                                    <CRow className='mt-3'>
                                        <CCol lg={6} md={6} sm={12}>
                                            <CFormSelect
                                                label="Document Type"
                                                value={document}
                                                onChange={(e) => setDocument(e.target.value)}
                                            >
                                                <option >Select</option>
                                                <option >Aadhar Card</option>
                                                <option >Pan Card</option>
                                                <option >Light Bill</option>
                                            </CFormSelect>
                                        </CCol>
                                        <CCol lg={6} md={6} sm={12}>
                                            <CFormSelect
                                                label="Staff Name"
                                                value={staffName}
                                                onChange={(e) => setStaffName(e.target.value)}
                                            >
                                                <option >Select</option>
                                                {staff.map((item, index) => (
                                                    <option key={index} value={item._id}>{item.FullName}</option>
                                                ))}
                                            </CFormSelect>
                                        </CCol>
                                        <CCol lg={6} md={6} sm={12}>
                                            <CFormInput
                                                className="mt-3 mr-3"
                                                type="file"
                                                onChange={handleImage}
                                                accept="image/*"
                                            />
                                        </CCol>
                                        <CCol className="mt-2" lg={6} md={6} sm={12}>
                                            <CButton className="mt-2" onClick={() => createGallery()}>Save</CButton>
                                        </CCol>
                                    </CRow>
                                </div>
                            }
                        </CForm>
                        <Table column={header} data={staff} />
                    </CCardBody>
                </CCard>
            </CCol>
        </CRow>
    )
}

export default EmpDocuments
